import React from 'react'
import './index.less'
import {Select, Form,Upload, Modal, Button, InputNumber, message, DatePicker, notification, Icon,Radio,Input} from 'antd';
import axios from "axios";
import {baseUrl} from "../../../config";
import moment from 'moment'
const FormItem = Form.Item;
const {RangePicker} = DatePicker;
const { TextArea } = Input;
const { Option } = Select

function getBase64(file) {
    return new Promise((resolve, reject) => {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = () => resolve(reader.result);
        reader.onerror = error => reject(error);
    });
}

class UploadQuestionForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            id_senting_work:'',
            // 
            previewVisible: false,
            previewImage: '',
            fileList:[],
        }
    }
    // 
    handleCancel = () => this.setState({ previewVisible: false });

    handlePreview = async file => {
        if (!file.url && !file.preview) {
            file.preview = await getBase64(file.originFileObj);
        }

        this.setState({
            previewImage: file.url || file.preview,
            previewVisible: true,
        });
    };

    handleChange = ({ fileList }) => this.setState({ fileList });

    componentDidMount() {
        // 
        axios.get(
            baseUrl + '/api/get_senting_work_id',
            {
                params: {
                    token: localStorage.customer_token,
                }
            })
            .then((Response) => {
                console.log('',Response.data);
                this.setState({
                    id_senting_work: Response.data.id,
                })
            })
    }
    handleSubmit = ()=>{
        // 
        let allVals= this.props.form.getFieldsValue()
        this.props.form.validateFields((err,values)=>{
            if (!err){
                console.log('', values);
                console.log({
                    'work_name': values.work_name,    //*
                    'work_tpl': values.work_tpl,  //*
                    'work_money': values.work_money,  //*
                    'work_person_number': values.work_person_number,    //*
                    'work_time': moment().unix(),   //*
                    'work_content': values.work_content,    //*
                    'stop_at': values.stop_at.unix(), //*
                    'token': localStorage.customer_token,//*
                    'id_senting_work': this.state.id_senting_work,   //get_senting_work_idid*
                    'work_url': values.work_url,    //
                    'file_num': this.state.fileList.length,  //
                    'begin_work_time': values.begin_work_time.unix(),  //*
                    'stop_work_time': values.stop_work_time.unix(),   //*
                    'id_communication': 0,
                })
                // 
                axios.get(
                    baseUrl + '/api/sent_work',
                    {
                        params: {
                            'work_name': values.work_name,    //*
                            'work_tpl': values.work_tpl,  //*
                            'work_money': values.work_money,  //*
                            'work_person_number': values.work_person_number,    //*
                            'work_time': moment().unix(),   //*
                            'work_content': values.work_content,    //*
                            'stop_at': values.stop_at.unix(), //*
                            'token': localStorage.customer_token,//*
                            'id_senting_work': this.state.id_senting_work,   //get_senting_work_idid*
                            'work_url': JSON.stringify(values.work_url),    //
                            'file_num': this.state.fileList.length,  //
                            'begin_work_time': values.begin_work_time.unix(),  //*
                            'stop_work_time': values.stop_work_time.unix(),   //*
                            'id_communication': 0,
                        }
                    })
                    .then((Response) => {
                        console.log(Response.data);
                        const d = Response.data
                        if (d.success){
                            notification.open({
                                message: d.success,
                                description: '',
                                placement: 'bottomLeft',
                                icon: <Icon type="smile" style={{ color: '#108ee9' }} />,
                            });
                            setTimeout(()=>{
                                document.location = `#/about/TaskPosted`
                            },1000)

                        }else {
                            message.error(d.sorry)
                        }
                    })
            }
        })
        console.log(allVals)
    }
    render() {
        const {getFieldDecorator} = this.props.form;
        const { previewVisible, previewImage, fileList } = this.state;
        const uploadButton = (
            <div>
                <Icon type="plus" />
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        return (
            <div>
                {/*<Card title=''>*/}
                <h2></h2>
                    <Form>
                        <FormItem label=''>
                            {getFieldDecorator('work_tpl',{
                                initialValue:'1',
                            })
                            (
                                <Select>
                                    <Option value="1"></Option>
                                    <Option value="2"></Option>
                                    <Option value="3"></Option>
                                    <Option value="0"></Option>
                                </Select>
                            )
                            }
                        </FormItem>
                        <FormItem label=''>
                            {getFieldDecorator('work_name',{
                                initialValue:'',
                                rules:[{
                                        required:true,
                                        message:''
                                }]
                                })
                            (<Input placeholder=''/>)
                            }
                        </FormItem>
                        <FormItem label=''>
                            {getFieldDecorator('work_money',{
                                initialValue:'',
                                rules:[{
                                        required:true,
                                        message:''
                                }]
                                })
                            (<Input placeholder=''/>)
                            }
                        </FormItem>
                        <FormItem  label=''>
                            {getFieldDecorator('work_person_number',{
                                initialValue:'',
                                rules:[{
                                    required:true,
                                    message:''
                                }]
                            })
                            (<InputNumber placeholder=''/>)
                            }
                        </FormItem>
                        <FormItem  label=''>
                            {getFieldDecorator('begin_work_time',{
                                initialValue:moment(),
                            })
                            (<DatePicker showTime format='YYYY-MM-DD HH:mm'/>)
                            }
                        </FormItem>
                        <FormItem  label=''>
                            {getFieldDecorator('stop_work_time',{
                                initialValue:moment(),
                            })
                            (<DatePicker showTime format='YYYY-MM-DD HH:mm'/>)
                            }
                        </FormItem>
                        <FormItem  label=''>
                            {getFieldDecorator('stop_at',{
                                initialValue:moment(),
                            })
                            (<DatePicker showTime format='YYYY-MM-DD HH:mm'/>)
                            }
                        </FormItem>

                        <FormItem  label='()'>
                            {getFieldDecorator('work_content')
                            (<TextArea placeholder=''/>)
                            }
                        </FormItem>
                        <FormItem  label='()'>
                            {getFieldDecorator('work_url',)
                            (<Input placeholder=''/>)
                            }
                        </FormItem>
                        <FormItem  label='()'>

                        <div>
                            <Upload
                                action={baseUrl + '/api/upload_work_file'}
                                listType="picture-card"
                                fileList={fileList}
                                onPreview={this.handlePreview}
                                onChange={this.handleChange}
                                data={{
                                    token: localStorage.customer_token,
                                    id_work: this.state.id_senting_work}}
                            >
                                {fileList.length >= 3 ? null : uploadButton}
                            </Upload>
                            <Modal visible={previewVisible} footer={null} onCancel={this.handleCancel}>
                                <img alt="example" style={{ width: '100%' }} src={previewImage} />
                            </Modal>
                        </div>
                        </FormItem>


                        {/*<FormItem label=''>*/}
                        {/*    {getFieldDecorator('workRange',{*/}
                        {/*        rules:[{*/}
                        {/*                required:true,*/}
                        {/*                message:''*/}
                        {/*        }]*/}
                        {/*        })*/}
                        {/*    (<RangePicker size='small' showTime format='YYYY-MM-DD HH:mm'/>)*/}
                        {/*    }*/}
                        {/*</FormItem>*/}
                        <FormItem>
                            <Button onClick={this.handleSubmit}></Button>
                        </FormItem>
                    </Form>
                {/*</Card>*/}
            </div>
        )
    }
}
export const UploadQuestion = Form.create()(UploadQuestionForm);